<template>
  <div class="main">
    <auto-header>
      <template v-slot:left>
        <div class="info hidden-sm-and-down">
          <back-button></back-button>
          <div>{{ proposalsNum }}</div>
        </div>
        <back-button class="hidden-md-and-up"></back-button>
      </template>
      <h1>{{ workspace.name }}</h1>
      <template v-slot:right>
        <rect-button
          class="hidden-sm-and-down"
          @click="checkEnvrionment"
          width="150px"
        >
          New proposal
        </rect-button>
        <rect-button
          class="hidden-md-and-up"
          width="48px"
          @click="checkEnvrionment"
        >
          <i class="el-icon-document-checked"></i>
        </rect-button>
      </template>
    </auto-header>
    <div class="home" v-loading="fullscreenLoading">
      <div class="list">
        <div class="list_title">
          <div class="select_items">
            <div
              v-for="(option, index) in filtrateList"
              class="select_item"
              :class="{ active: status == option }"
              :key="index"
              @click="filtrate(option)"
            >
              {{ option }}
            </div>
          </div>
        </div>
        <div
          class="list_item"
          v-for="(proposal, index) in workspace.proposals"
          @click="toDetail(proposal)"
          :key="index"
          v-show="status === 'All' || proposal.status === status ? true : false"
        >
          <div class="up">
            <div
              class="state"
              :style="{
                background: proposal.status == 'Active' ? '#21b66f' : '#ff4231'
              }"
            >
              {{ proposal.status }}
            </div>
            <div class="title" v-html="proposal.title"></div>
          </div>
          <div
            class="down"
            v-html="
              `Start ${dateString(proposal.start)} end ${dateString(
                proposal.end
              )}`
            "
          ></div>
        </div>
      </div>
      <metamask-dialog
        :showDialog="showMetamaskDialog"
        @onClose="showMetamaskDialog = false"
      ></metamask-dialog>
      <custom-rpc-dialog
        :showDialog="showRpcDialog"
        @onClose="showRpcDialog = false"
      ></custom-rpc-dialog>
    </div>
  </div>
</template>

<script>
import { checkMetaMask } from "../matalab/witness.js";
import Network from "../matalab/global";
import AutoHeader from "../components/AutoHeader.vue";
import RectButton from "../components/RectButton.vue";
import MetamaskDialog from "../components/MetamaskDialog.vue";
import CustomRpcDialog from "../components/CustomRpcDialog.vue";
import BackButton from "../components/BackButton.vue";
import { mapGetters } from "vuex";

export default {
  data() {
    return {
      status: "All",
      filtrateList: ["All", "Active", "Closed"],
      showMetamaskDialog: false,
      showRpcDialog: false,
      correctNetWork: false
    };
  },
  components: {
    AutoHeader,
    RectButton,
    MetamaskDialog,
    CustomRpcDialog,
    BackButton
  },
  async mounted() {
    const id = this.$route.params.id;
    const name = this.$route.params.name;
    const contract = this.$route.params.contract;
    this.$store.dispatch("changeWorkspace", {
      id: id,
      name: name,
      contract: contract
    });
  },
  methods: {
    filtrate(status) {
      this.status = status;
    },
    toDetail(proposal) {
      this.$router.push({
        name: "proposal",
        params: { proposalId: proposal.id, proposal: proposal }
      });
    },
    toNewProposals() {
      this.$router.push({
        name: "newProposals",
        params: { id: this.workspace.id, name: this.workspace.name }
      });
    },
    async checkEnvrionment() {
      this.showRpcDialog = false;
      this.showMetamaskDialog = false;
      const installed = await checkMetaMask();
      if (installed) {
        let chainId = await window.ethereum.request({ method: "eth_chainId" });
        if (parseInt(chainId) === Network.automataChainId) {
          this.toNewProposals();
        } else {
          this.showRpcDialog = true;
        }
      } else {
        this.showMetamaskDialog = true;
      }
    },
    dateString(timestamp) {
      const date = new Date(parseInt(timestamp) * 1e3);
      let dateString = date.toLocaleString().split(",")[0];
      if (dateString === "Invalid Date") {
        dateString = "Unknown";
      }
      return dateString;
    }
  },
  computed: {
    proposalsNum() {
      if (this.workspace && this.workspace.proposals) {
        let proposalsLength = this.workspace.proposals.length;
        if (proposalsLength > 0) {
          return `Proposals(${proposalsLength})`;
        } else {
          return "Proposals";
        }
      } else {
        return "Proposals";
      }
    },

    workspace() {
      if (this.activeWorkspace.id === this.$route.params.id) {
        return this.activeWorkspace;
      } else {
        return {};
      }
    },
    fullscreenLoading: {
      get: function() {
        return this.loadProposals;
      },
      set: () => {}
    },
    ...mapGetters({
      activeWorkspace: "activeWorkspace",
      loadProposals: "loadProposals"
    })
  }
};
</script>

