<template>
  <el-dialog
    title="Change Network"
    center
    style="font-weight: bold"
    :visible.sync="dialogVisiable"
    custom-class="dialog-style"
    width="400px"
    :fullscreen="isSmallScreen"
  >
    <div class="detail">
      <img class="image" src="../assets/img/favicon.png" alt="metamask" />
      <div class="warn">
        You need to connect to Automata network to continue.
        <a
          href="https://metamask.zendesk.com/hc/en-us/articles/360043227612-How-to-add-custom-Network-RPC-and-or-Block-Explorer"
          target="_blank"
        >
          Click here</a
        >
        to check how to setup custom network for Metamask.
      </div>
      <div class="ata-info">
        <div class="state">
          <div class="title">Automata network</div>
        </div>
        <div class="state">
          <div class="address">
            <div class="content">RPC URL</div>
          </div>
          <div class="address">
            <div class="content">{{ address }}</div>
            <transition name="slide-fade"
              ><div class="content copy" v-show="copied">
                Copied!
              </div></transition
            >
            <el-button
              class="btn"
              size="mini"
              @click="copyAddress()"
              :data-clipboard-text="address"
            >
              <i class="el-icon-document-copy"></i>
            </el-button>
          </div>
        </div>
        <div class="state">
          <div class="content">Chain ID</div>
          <div>{{ chainId }}</div>
        </div>
        <div class="state">
          <div class="content">Currency Symbol</div>
          <div>ATA</div>
        </div>
      </div>
    </div>
  </el-dialog>
</template>

<script>
import ClipboardJS from "clipboard";
import Network from "../matalab/global";

export default {
  props: {
    showDialog: { type: Boolean, default: false }
  },
  data() {
    return {
      address: Network.automataAddress,
      chainId: Network.automataChainId,
      copied: false
    };
  },
  computed: {
    dialogVisiable: {
      get: function() {
        return this.showDialog;
      },
      set: function(value) {
        this.copied = false;
        this.$emit("onClose", false);
        return value;
      }
    },
    isSmallScreen() {
      return window.innerWidth < 450;
    }
  },
  methods: {
    openLink(e) {
      window.open(e);
      this.$emit("onClose", false);
    },
    async copyAddress() {
      let clipboard = new ClipboardJS(".btn");
      let that = this;
      clipboard.on("success", function(e) {
        console.debug(e);
        that.copied = true;
      });
    }
  }
};
</script>

